<template>
  <div class="about-template">
    <div class="g-in">
      <div class="index-info clearfix">
        <el-button class="fr" size="mini" @click="goVersionList">查看历史版本</el-button>
      </div>
      <div class="version-info">
        <span class="new-version" v-if="leastVersion">最新版本</span>
        <div class="info-top">
          <h3>{{ info.title }}</h3>
          <p><span>指标体系</span>&emsp;<span>{{ info.saveTime&&info.saveTime.split(' ')[0] }}</span></p>
        </div>
        <div class="info-content" v-html="info.content"></div>
      </div>
    </div>
    <Drawer title="历史版本" width="30" :closable="false" v-model="drawerValue">
      <ul class="version-list">
        <li class="clearfix" :style="versionList" v-for="item in allList" :key="item.id" @click="clickVersion(item)">
          <span :style="versionTitle">{{item.title}}</span>
          <span class="fr">{{item.saveTime&&item.saveTime.split(' ')[0]}}</span>
        </li>
      </ul>
    </Drawer>
    <BackTop></BackTop>
  </div>
</template>

<script>
export default {
  data() {
    return {
      info: {},
      allList: [],
      drawerValue: false,
      versionList: {
        height: '36px',
        lineHeight: '36px',
        cursor: 'pointer'
      },
      versionTitle: {
        display: 'inline-block',
        width: '70%',
        whiteSpace: 'nowrap',
        overflow: 'hidden',
        textOverflow: 'ellipsis'
      },
      leastVersion: true
    }
  },
  created() {
    this.getAllInfoList()
  },
  methods: {
    async getAllInfoList() {
      const res = await this.$ajaxPost(this.projectName + '/document/finds.do')
      // console.log(res)
      this.allList = res
      this.allList.forEach((el, index) => {
        this.$set(el, 'index', index)
      })
      this.info = this.allList[0]
    },
    goVersionList() {
      this.drawerValue = true
    },
    clickVersion(item) {
      // console.log(item)
      this.leastVersion = item.index === 0
      this.info = item
      this.drawerValue = false
    }
  }
}
</script>

<style lang="scss">
.about-template{
  .index-info{
    height: 34px;
    line-height: 34px;
    box-sizing: border-box;
    padding: 0 10px;
    margin: 14px 0;
    .el-button{
      margin-top: 3px;
    }
  }
  .version-info{
    box-sizing: border-box;
    padding: 20px 40px;
    background-color: #fff;
    margin: 14px 0 32px;
    position: relative;
    .new-version{
      position: absolute;
      top: 0;
      left: 0;
      background-color: #00aec4;
      padding: 6px 10px;
      color: #fff;
    }
    .info-top{
      text-align: center;
      padding-bottom: 10px;
      border-bottom: 1px solid #dbdbdb;
      h3{
        font-size: 20px;
        font-weight: 700;
      }
    }
    .info-content{
      padding-top: 20px;
    }
  }
  .version-list {
    li{
      height: 36px;
      line-height: 36px;
    }
  }
}
</style>



